import React from 'react';

import ImageBanner from '../../components/ImageBanner/ImageBanner.js';
import bannerImage from './banner-image-compressed.jpg';

import '../pages.css';
import './project-page.css';

import ProjectCard from './Card.js';
import projectData from './database.js';


class ProjectPage extends React.PureComponent {
  renderProjects() {
    const projectCards = [];
    for (const [key, data] of Object.entries(projectData)) {
      projectCards.push(
	<ProjectCard key={key} data={data}/>
      );
    }
    return projectCards;
  }

  render() {
    return (
      <div id='page-container'>
	<ImageBanner image={bannerImage}>
	  <h1>Projects</h1>
	  <p>A library of the small side projects I've compiled over the years.</p>
	</ImageBanner>
	<div id='content-container'>
	 {this.renderProjects()}
	</div>
      </div>
    );
  }
}

export default ProjectPage;



